body {
  margin: 0;
  padding: 0;
  font-family: "Calibri", "Roboto", sans-serif;
}

.typing{
  position: fixed;
  bottom: 90px;
  left: 10px;
}

.wave p{
  position: relative;
  top: 10px;
  display: inline-block;
}

.wave span {
  position: relative;
  top: 10px;
  display: inline-block;
  -webkit-animation: bounce .5s ease infinite alternate;
          animation: bounce .5s ease infinite alternate;
  font-family: "Calibri", "Roboto", sans-serif;
  font-size: 16px;
  color: grey;
}

.wave span:nth-child(2) { -webkit-animation-delay: .1s; animation-delay: .1s; }
.wave span:nth-child(3) { -webkit-animation-delay: .2s; animation-delay: .2s; }
.wave span:nth-child(4) { -webkit-animation-delay: .3s; animation-delay: .3s; }
.wave span:nth-child(5) { -webkit-animation-delay: .4s; animation-delay: .4s; }

@-webkit-keyframes bounce {
  100% {
    top: 7px;
  }
}

@keyframes bounce {
  100% {
    top: 7px;
  }
}
* {
  box-sizing: border-box;
}

body {
  background-color: #b0d6ff;
  font-family: "Calibri", "Roboto", sans-serif;
}
#root{
  height: 100%;
}
.chat_window {
  position: absolute;
  width: calc(100% - 20px);
  max-width: 800px;
  height: 600px;
  border-radius: 10px;
  background-color: #fff;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  background-color: #f8f8f8;
  overflow: hidden;
  margin-top: 10px;
}
.sign-out{
  z-index: 9;
  position: absolute;
  top: 0px;
  font-size: 20px;
  background-color: #e4e4e4;
  padding: 5px;
  border-bottom-right-radius: 10px;
}
.top_menu {
  background-color: #fff;
  width: 100%;
  padding: 20px 0 15px;
  box-shadow: 0 1px 30px rgba(0, 0, 0, 0.1);
}
.top_menu .buttons {
  margin: 3px 0 0 20px;
  position: absolute;
}
.top_menu .buttons .button {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 10px;
  position: relative;
}
.top_menu .buttons .button.close {
  background-color: #f5886e;
}
.top_menu .buttons .button.minimize {
  background-color: #fdbf68;
}
.top_menu .buttons .button.maximize {
  background-color: #a3d063;
}
.top_menu .title {
  text-align: center;
  color: #bcbdc0;
  font-size: 20px;
}
.d-none{
  display: none;
}
.kep-login-facebook{
  border-radius: 15px !important;
}
.messages {
  position: relative;
  list-style: none;
  padding: 20px 10px 0 10px;
  margin: 0;
  max-height: 500px;
  overflow: auto;
}
.messages .message {
  clear: both;
  overflow: hidden;
  margin-bottom: 20px;
  transition: all 0.5s linear;
  opacity: 0;
}
.messages .message.left .avatar {
  background-color: #6fb2e8;
  float: left;
}
.messages .message.left .text_wrapper {
  background-color: #cccccc;
  margin-left: 20px;
}
.messages .message.left .text_wrapper::after, .messages .message.left .text_wrapper::before {
  right: 100%;
  border-right-color: #cccccc;
}
.messages .message.left .text {
  color: black;
}
.messages .message.right .avatar {
  background-color: #fdbf68;
  float: right;
}
.messages .message.right .text_wrapper {
  background-color: #c7eafc;
  margin-right: 20px;
  float: right;
}
.messages .message.right .text_wrapper::after, .messages .message.right .text_wrapper::before {
  left: 100%;
  border-left-color: #c7eafc;
}
.messages .message .is-mine{
  max-width: calc(100% - 105px) !important;
}
.is-mine{
  float: right !important;
  background-color: #6fb2e8 !important;
}
.is-mine .text{
  color: white !important;
}
.is-mine.nameTag{
  margin-right: 90px;
  background-color: transparent !important;
}
.messages .message.left .is-mine {
  margin-left: 0px;
  margin-right: 20px !important;
}
.messages .message.right .is-mine::after,  .messages .message.right .is-mine::before {
  border-right-color:white !important;
}
.messages .message.left .is-mine::after, .messages .message.left .is-mine::before {
  border-left-color: #6fb2e8 !important;
  border-right-color:transparent !important;
  left: 100% !important;
}
.messages .message.right .text {
  color: #45829b;
}
.messages .message.appeared {
  opacity: 1;
}
.messages .message .avatar {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.messages .message .text_wrapper {
  display: inline-block;
  padding: 11px;
  border-radius: 6px;
  max-width: calc(100% - 85px);;
  min-width: 100px;
  position: relative;
}
.messages .message .text_wrapper::after, .messages .message .text_wrapper:before {
  top: 18px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.messages .message .text_wrapper::after {
  border-width: 13px;
  margin-top: -7px;
}
.messages .message .text_wrapper::before {
  border-width: 13px;
  margin-top: -7px;
}
.nameTag{
    margin-left: 70px;
    color: #868080;
}
.messages .message .text_wrapper .text {
  word-wrap: break-word;
  font-size: 18px;
  font-weight: 300;
}
.containButton{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.containFB{
  width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
