}
.emoji-mart{
  z-index: 99;
  position: fixed;
  top: 0px;
}
.emoji-mart-preview{
  display: none;
}
.emoji-mart-scroll{
  height: 380px;
}
.bottom_wrapper .message_input_wrapper .message_input {
  border: none;
  margin-left: 30px;
  height: 100%;
  box-sizing: border-box;
  width: calc(100% - 60px);
  position: absolute;
  outline-width: 0;
  color: gray;
}
.icon{
  font-size: 25px;
  position: relative;
  top: 11px;
  z-index: 9;
}
.bottom_wrapper .send_message {
  width: 100px;
  height: 50px;
  display: inline-block;
  border-radius: 50px;
  background-color: #a3d063;
  border: 2px solid #a3d063;
  color: #fff;
  cursor: pointer;
  transition: all 0.2s linear;
  text-align: center;
  float: right;
}
.bottom_wrapper .send_message:hover {
  color: #a3d063;
  background-color: #fff;
}
.bottom_wrapper .send_message .text {
  font-size: 18px;
  font-weight: 300;
  display: inline-block;
  line-height: 48px;
}

.message_template {
  display: none;
}

